<template>
  <p>
    {{ textBeforeAction }}
    <br>
    <a href='#' :class="['link', className]" id='again' @click=sendOneMoreWish>{{ callToActionText }}</a>
  </p>

</template>

<script>
  import { mapActions } from 'vuex'

  export default {
    name: 'OneMoreWish',
    props: {
      textBeforeAction: String,
      callToActionText: String,
      isVisible: Boolean,
    },
    computed: {
      className() { return this.isVisible ? '' : 'hide' },
    },
    methods: {
      sendOneMoreWish () {
        this.SET_WISH_SENT({ isSent: false })
      },
      ...mapActions([
        'SET_WISH_SENT'
      ])
    },
  }
</script>

<style scoped>
  a,
  a:hover {
    text-decoration: none;
  }

  .link {
    display: inline-block;
    color: #fff;
    border-bottom: 1px dashed #fff;
  }

  .link.hide {
    position: relative;
    transform: translate(0, 50px);
    color: transparent;
    text-shadow: 0 1px 3px transparent;
    border-bottom: none;
  }
</style>